import {mapState,mapMutations,mapGetters} from 'vuex';
export default {
	data() {
		return {
			isLogin:false
		};
	},
	onShow(){
		// console.log('onShow',uni.getStorageSync('userInfo'))
		this.isLogin = Boolean(uni.getStorageSync('userInfo'))
	},
	computed:{
		...mapState({
			cartList:state=>state.cart.cartList
		}),
		...mapGetters(["isSelectedAll"])
	},
	methods:{
		...mapMutations(["CHANGESHOPITEMCOUNTMUTATION","CHANGESHOPITEMSELECTEDMUTATION","CHANGEALLSELECTEDMUTATION"]),
		addShopItemCount(type,index){
			// console.log('addShopItemCount',type,index)
			this.CHANGESHOPITEMCOUNTMUTATION({type,index})
		},
		changeSelected(selected,index){
			this.CHANGESHOPITEMSELECTEDMUTATION({selected,index});
		},
		changeAllSelected(selected){
			this.CHANGEALLSELECTEDMUTATION(selected);
		}
	}
}
